import { Component, OnInit, Input } from '@angular/core';

import { CompanyService } from '../company.service';
import { Company } from '../company';
import { BeneficialOwner } from '../beneficialOwner';

@Component({
  selector: 'app-company-detail',
  templateUrl: './company-detail.component.html',
  styleUrls: ['./company-detail.component.css']
})
export class CompanyDetailComponent implements OnInit {

  private _company: Company;

  beneficialOwnerName: string;

  beneficialOwners: BeneficialOwner[];

  constructor(private companyService: CompanyService) { }

  ngOnInit() {
  }

  @Input() set company(company: Company) {
    this._company = company;
    this.getBeneficialOwners();
  }

  get company() {
    return this._company;
  }

  save() {
    this.companyService.updateCompany(this.company).subscribe();
  }

  delete() {
    this.companyService.deleteCompany(this.company.id).subscribe(() => this.reload());
  }

  addBeneficialOwner() {
    this.companyService.addBeneficialOwner(this.beneficialOwnerName, this.company.id, () => this.reload());
  }

  getBeneficialOwners() {
    this.companyService.getBeneficialOwners(this.company.id).subscribe(owners => this.beneficialOwners = owners);
  }

  private reload() {
    location.reload();
  }
  
}



