import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'

import {Company} from './company';
import {BeneficialOwner} from './beneficialOwner';
import { MessageService } from './message.service';
import { catchError, tap } from 'rxjs/operators';

@Injectable()
export class CompanyService {

  private companiesUrl = '/companies';

  private httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(private httpClient : HttpClient, private messageService : MessageService) { }

  getCompanies(): Observable<Company[]> {
    return this.httpClient.get<Company[]>(this.companiesUrl)
    .map((result:any)=>{
      return result._embedded.companies;
    })
    .pipe(
      catchError(this.handleError('Error while getting companies ompanies', []))
    );
  }

  getBeneficialOwners(companyId: number): Observable<BeneficialOwner[]> {
    const url = `${this.companiesUrl}/${companyId}/beneficialOwners`;
    return this.httpClient.get<BeneficialOwner[]>(url)
    .map((result:any)=>{
      return result._embedded.benf_owners;
    })
    .pipe(
      catchError(this.handleError('Error while getting beneficial owners', []))
    );
  }

  getCompany(id: number) {
    const url = `${this.companiesUrl}/${id}`;
    return this.httpClient.get<Company>(url).pipe(
      tap(_ => this.log(`fetched company id=${id}`)),
      catchError(this.handleError<Company>(`getCompany id=${id}`))
    );
  }

  updateCompany(company: Company) {
    const url = `${this.companiesUrl}/${company.id}`;
    return this.httpClient.put<Company>(url, company, this.httpOptions).pipe(
      tap(_ => this.log(`Updated company id=${company.id}`)),
      catchError(this.handleError<Company>(`updateCompany id=${company.id}`))
    );
  }

  addCompany(company: Company) {
    return this.httpClient.post<Company>(this.companiesUrl, company, this.httpOptions).pipe(
      tap(_ => this.log('Company created')),
      catchError(this.handleError<Company>('Error while creating company'))
    );
  }

  deleteCompany(id: number) {
    const url = `${this.companiesUrl}/${id}`;
    return this.httpClient.delete(url).pipe(
      tap(_ => this.log(`deleted company id=${id}`)),
      catchError(this.handleError<Company>(`deleteCompany id=${id}`))
    );
  }

  addBeneficialOwner(name: string, companyId: number, onSuccess: Function) {
    const url = '/benf_owners';
    this.httpClient.post<BeneficialOwner>(url, new BeneficialOwner(name), this.httpOptions).pipe(
      tap(_ => this.log(`Created Beneficial Owner with name ${name}`)),
      catchError(this.handleError('Error while creating beneficial owner'))
    ).subscribe((result : BeneficialOwner) => {
      const addOwnerToCompanyUrl = `${url}/${result.id}/company`
      const body = `${window.location.hostname}:${window.location.port}/${this.companiesUrl}/${companyId}`;
      const addOwnerHttpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'text/uri-list' })
      };
      this.httpClient.put(addOwnerToCompanyUrl, body, addOwnerHttpOptions).pipe(
        tap(_ => this.log(`Associated Beneficial Owner to Company ${companyId}`)),
        catchError(this.handleError(`Error while associated Beneficial Owner to Company ${companyId}`))
      ).subscribe(onSuccess());
    });
  }

  private log(message: string) {
    this.messageService.add('CompanyService: ' + message);
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
   
      console.error(error);
   
      this.log(`${operation} failed: ${error.message}`);
   
      return Observable.of(result as T);
    };
  }
}



